___CSS_LOADER_EXPORT___.push([module.id, ".fade-enter-active[data-v-43d2546d],.fade-leave-active[data-v-43d2546d]{transition:opacity .5s}.fade-enter[data-v-43d2546d],.fade-leave-to[data-v-43d2546d]{opacity:0}.product-image[data-v-43d2546d]{cursor:pointer;position:relative;transition:all .2s ease-in;z-index:20}.product-image.selected[data-v-43d2546d]{transform:scale(1.2) translateY(-20px);z-index:30!important}.product-wrapper[data-v-43d2546d]{position:relative;z-index:30}", "",{"version":3,"sources":["webpack://./resources/js/frontend/components/Products/ImageWrapper.vue"],"names":[],"mappings":"AA2FA,wEAEA,sBACA,CACA,6DAEA,SACA,CAEA,gCAEA,cAAA,CAEA,iBAAA,CAHA,0BAAA,CAEA,UAEA,CAEA,yCACA,sCAAA,CACA,oBACA,CAEA,kCACA,iBAAA,CACA,UACA","sourcesContent":["<template>\n    <div class=\"flex justify-center h-full\" v-show=\"sorted_products.length > 0\">\n        <div v-for=\"(product, index) in sorted_products\" :key=\"product.id\" class=\"flex flex-col justify-end\">\n            <!-- <div @mouseover=\"hoverProduct(index)\" @mouseout=\"hoverProduct(-1)\" data-aos=\"fade-right\" data-aos-easing=\"ease-out-cubic\" :data-aos-duration=\"500\" :data-aos-delay=\"150 + (2 * index)\"> -->\n            <div @mouseover=\"hoverProduct(index)\" @mouseout=\"hoverProduct(-1)\" @click=\"showProduct(product)\" class=\"shadow-2xl\">\n                <div class=\"product-wrapper\">\n                    <img :src=\"generateImageCacheUrl(product)\"\n                        :alt=\"product.name\"\n                        :ref=\"`product-image-${product.id}`\" \n                        class=\"product-image\" \n                        :class=\"[{'selected': isSelected(index)}, getImageWidth(product.size)]\" \n                        v-tooltip.bottom-start=\"tooltipContent(product)\">\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\n        'products',\n        'nav_selected',\n    ],\n\n    data(){\n        return {\n            sorted_products: [],\n            selectedIdx: -1,\n        }\n    },\n\n    watch: {\n        nav_selected(new_val){\n            if (new_val > 0) {\n                //sort by products.size\n                this.sorted_products = _.orderBy(this.products, 'size', 'desc');\n            }else{\n                setTimeout(() => {\n                    this.sorted_products = this.products;\n                }, 400)\n            }\n        },\n    },\n\n    methods: {\n        //image width by product size\n        getImageWidth(size){\n            if (size >= 1000) {\n                return 'product-image-large';\n            }else if (size >= 200) {\n                return 'product-image-medium';\n            }else if (size >= 125) {\n                return 'product-image-small';\n            }\n            return 'product-image-large';\n        },\n\n        hoverProduct(idx){\n            this.selectedIdx = idx;\n        },\n\n        isSelected(idx){\n            return this.selectedIdx == idx;\n        },\n\n        tooltipContent(product){\n            return {\n                'content': product.name,\n                'theme': 'tooltip-product',\n                'skidding': 30,\n                'distance': -20,\n            };\n        },\n\n        showProduct(product){\n            const slug = product.slug;\n            const url = `/varian-milklife/${slug}`;\n            window.location.href = url;\n        },\n\n        // Geneate cache by adding parameter_updated_at to image url\n        generateImageCacheUrl(product){\n            return `${product.image}?updated_at=${product.updated_at}`;\n        }\n    },\n\n}\n</script>\n\n<style scoped>\n    .fade-enter-active,\n    .fade-leave-active {\n       transition: opacity .5s;\n    }\n    .fade-enter,\n    .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {\n        opacity: 0;\n    }\n\n    .product-image{\n        transition: all .2s ease-in;\n        cursor: pointer;\n        z-index: 20;\n        position: relative;\n    }\n\n    .product-image.selected{\n        transform: scale(1.2) translate(0, -20px);\n        z-index: 30 !important;\n    }\n\n    .product-wrapper{\n        position: relative;\n        z-index: 30;\n    }\n\n\n</style>\n"],"sourceRoot":""}]);
